<template>
  <div>
    <div class="container name">M A N I F E S T O Avalon Maps </div>
  <div class="container"> 
    <form id="search" autocomplete="off">
    <input name="query" type="text" ref='input' class="search-ipt" placeholder="Search" v-model="busca">
    <span class="criador"> created by mindyngo </span>
  </form>
  </div>

  <div class="container"> 
  <div class='container-table'>
     <table>
      <thead>
        <tr>
          <th>Nome</th>
          <th>Tier</th> 
          <th>Tipo</th>
          <th>Recursos</th>
        </tr>
        </thead>
        <tbody v-if="Busca.length > 1">
          <tr v-for="(mapa, index) in Busca" :key="index">
            <td>{{mapa["@displayname"]}}</td>
            <td>{{mapa["@tiermap"]}}</td>
            <td>{{mapa["@type"]}}</td>
        
            <td>
              <span v-for="(marker, index2) in mapa.minimapmarkers.marker" :key="index2">
                <img :src='publicPath+"images/"+marker["@type"]+".png"'>
              </span>
            </td>
          </tr>
        </tbody>
      </table>
    
  </div>
  </div>
  </div>
</template>

<script>

import AlbionData from "./js/cluster.js";

export default {
  name: 'Mapas',
  data() {
    return {
      publicPath: process.env.BASE_URL,
      busca:"",
      mapas:[],
      // filtro:"roads_of_avalon_raid_pve",
      filtro:"",
    }
    },
     computed: {
      Busca(){
      const busca = this.busca && this.busca.toLowerCase()
      const filtro = this.filtro
      let data =  AlbionData.cluster
      let mapas = []
      let status = 0
      if (filtro) {
        data.forEach((ele, i) =>{
          let self = ele
          let marcadores = self.minimapmarkers.marker
          marcadores.forEach((el, j) =>{
            if(el["@type"] == filtro){
              status += 1
            }
          })
          if(status > 0){
            mapas.push(self)
           }
           status = 0
        } )
        data = mapas
      }
        if (busca) {
          data = data.filter((row) => {
          return Object.keys(row).some((key) => {
            return String(row[key]).toLowerCase().indexOf(busca) > -1
          })
          })
        }
      return data
      },
     },
       mounted() {
    this.$refs.input.focus()
    console.log(this.$refs);
 
  },

  created(){
  var mapas = AlbionData.cluster
            mapas.forEach(element => {
              element["@tiermap"] = element['@file'].split('_')[4]
      });
      // this.$refs.busca.focus();
    
    },
    methods: {

    },

  }
</script>

